export default {
  transition: 'telecoms',
  data() {
    return {
      heroAlt: [
        {
          src: '',
          heading: ' TELECOMS ',
          icon: '',
          image: 'telecoms.svg',
        },
      ],
      search: '',
      filteredList: [],
    }
  },
  created() {
    this.getAllMusics()
  },
  methods: {
    async getAllMusics() {
      this.listLoading = true
      const config = {
        headers: {
          'Access-Control-Allow-Origin': '*',
        },
      }
      try {
        const { data } = await this.$axios.get(
          'https://cbsbackend.herokuapp.com/api/products',
          config
        )

        // console.log('here is the data', data)

        data.forEach((product) => {
          if (product.category === 'Telecoms') {
            this.filteredList.push(product)
          }
        })

        // console.log(this.filteredList)
        // console.log('here is the category', data.forEach())

        // this.filteredList.push(
        //   data.filter((category) => category !== 'Fibre Blowing')
        // )

        return this.filteredList
      } catch (err) {
        throw new Error('Error Fetching Products')
      }
    },
  },
}
